<template>
  <Container @drop="onDrop"
    :group-name="'elements'"
    class="full-size"
    drag-class="my-drag" drop-class="my-drop"
    :get-child-payload="getPayload">
    <BaseElement v-for="el in getValue('Elements')"
        :key="el.name"
        :element="el">
    </BaseElement>
  </Container>
</template>

<script>
import {
  ElementModel,
  PropertyModel,
  StateModel,
  Types
} from "../ElementModel.vue";
import BaseElement from "../BaseElement.vue";
import { Container } from "vue-smooth-dnd";

export default {
  name: "container-element",
  props: ["element"],
  created() {
    this.$store.commit("addProperties", {
      element: this.element,
      properties: [new PropertyModel("Elements", [])]
    });
    this.setValue("Width", "100%");
    this.setValue("Height", "100%");
    this.setValue("Top", 0);
    this.setValue("Left", 0);
  },
  methods: {
    getValue(propertyName) {
      return this.$store.getters.getValue(this.element, propertyName);
    },
    setValue(propertyName, newValue) {
      return this.$store.commit("setValue", {
        element: this.element,
        propertyName: propertyName,
        newValue: newValue
      });
    },
    onDrop(dropResult) {
      let applyDrag = (arr, dragResult) => {
        const { removedIndex, addedIndex, payload, droppedElement } = dragResult;
        if (removedIndex === null && addedIndex === null) return arr;
        const result = [...arr];
        let itemToAdd = payload;
        if (removedIndex === null && addedIndex !== null && !itemToAdd.container) {
          if(itemToAdd.name && itemToAdd.component) {
            let newElement = new ElementModel(
              `${itemToAdd.name} ${result.length}`,
              itemToAdd.component
            );
            this.$store.getters.addElement(this.element, addedIndex, newElement);
            return result;
          }
        }
        if (removedIndex !== null) {
          this.$store.getters.removeElement(itemToAdd.container, removedIndex);
        }
        if (addedIndex !== null) {
          this.$store.getters.addElement(this.element, addedIndex, itemToAdd.element);
        }
        return result;
      };
      let elements = this.getValue("Elements");
      let newElements = applyDrag(elements, dropResult);
    },
    getPayload(index) {
      return { 
        element: this.getValue("Elements")[index],
        container: this.element
      };
    }
  },
  components: {
    BaseElement,
    Container
  }
};
</script>

<style>
.my-drag {
  transition: all .18s ease;
  opacity: 0.8;
  transform: rotateZ(1deg);
  background-color: white;
  font-family: 'Roboto';
}

.my-drop {
  opacity: 1;
  transform: rotateZ(0deg);
  background-color: white;
  font-family: 'Roboto';
}
</style>



